.dress-section {
  width: 100%;
  min-height: auto;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Playfair Display", Georgia, serif;
}

/* Главная карточка */
.dress-card {
  background-color: #7a1a1a;
  width: 100%;
  max-width: 380px;
  color: #fdfdfd;
  text-align: center;
  border-radius: 200px 200px 0px 0px;
  overflow: hidden;
  box-shadow: none;
  position: relative;
  padding: 50px;
  margin: 0;
  
}

/* Арка — создаём с помощью псевдоэлемента */


/* Чтобы форма выглядела как полукруг, а не прямоугольник */
.dress-arc::before {
  content: "";
  position: absolute;
  top: -100px;
  left: 50%;
  transform: translateX(-50%);
  width: 200%;
  height: 200px;
  background: #7a1a1a;
  border-radius: 5000%;
  z-index: -1;
}

/* Заголовок */
.dress-title {
  font-family: "Great Vibes", "Playfair Display", serif;
  font-size: 2.6rem;
  font-weight: 400;
  color: rgba(255,255,255,0.9);
  margin-top: 10px;

}

/* Основной текст */
.dress-text {
  font-size: 1rem;
  line-height: 1.5;
  color: rgba(255,255,255,0.9);
  margin: 40px 20px 36px;
  font-family: "Playfair Display", Georgia, serif;
  margin-top: 0px;
}

/* Палитра */
.dress-palette {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

/* Один цвет */
.dress-color {
  display: flex;
  align-items: center;
  gap: 20px;
  color: rgba(255,255,255,0.9);
  font-size: 1rem;
  letter-spacing: 0.04em;
}

/* Мазок цвета */
.paint {
  width: 110px;
  height: 28px;
  border-radius: 999px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 10px rgba(0,0,0,1.1);
}

/* Эффект неровных краёв кисти */
.paint::before {
  content: "";
  position: absolute;
  inset: 0;
  background: inherit;
  mask-image: radial-gradient(circle at 0% 50%, black 70%, transparent 100%);
  mask-size: 300% 100%;
  mask-repeat: no-repeat;
  opacity: 0.9;
}

/* Цвета */
.paint.white { background: #f6f6f6; }
.paint.beige { background: #d9b88f; }
.paint.gray  { background: #5c5b5b; }

/* адаптив */
@media (max-width: 420px) {
  .dress-card {
    max-width: 340px;
  }

  .dress-title {
    font-size: 2.2rem;
  }

  .dress-text {
    font-size: 0.95rem;
    margin: 24px 16px 30px;
  }

  .paint {
    width: 90px;
    height: 24px;
  }
}
