/* ВЕСЬ ЭКРАН: без внешних отступов, без скролла воздуха снизу */
.invite-page {
  width: 100%;
  height: 100vh;
  min-height: 100vh;

  background: radial-gradient(circle at 50% 0%, #ffffff 0%, #f3f3f3 70%);

  display: flex;
  justify-content: center;
  align-items: flex-start;

  padding: 0;
  margin: 0;

  color: #1f1f1f;
  font-family: "Playfair Display", Georgia, serif;
  box-sizing: border-box;
}

/* КАРТОЧКА, которая теперь = весь экран */
.invite-card {
  position: relative;

  width: 100%;
  max-width: 420px;

  height: 100%;
  min-height: 100vh;

  background: #fff;
  box-sizing: border-box;

  overflow: hidden;

  /* лёгкая тень как в твоём скрине под основной белой областью */
  box-shadow: 0 40px 80px rgba(0,0,0,0.15);
}

/* SVG рамка теперь растягиваем поверх всей карточки */
.invite-border {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

/* текст по дуге сверху */
.arc-text {
  font-family: "Playfair Display", serif;
  font-size: 10px;
  letter-spacing: 0.15em;
  fill: #7a1a1a;
  text-transform: uppercase;
}

/* Вёрстка контента внутри карточки:
   сверху имена,
   по центру фото,
   внизу дата с линией
*/
.invite-layout {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  height: 100%;
  width: 100%;

  padding-top: 80px;   /* отступ от дуги с текстом до имён */
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 24px;

  box-sizing: border-box;
  text-align: center;
}

/* Блок имён в рукописном стиле */
.names {
  position: relative;
  font-family: "Great Vibes", cursive;
  color: #1f1f1f;
  text-align: center;
  line-height: 1.2;
  margin-bottom: 32px;
}

.name-left {
  font-size: 2.2rem;
  font-weight: 400;
}

.name-right {
  font-size: 2.2rem;
  font-weight: 400;
  margin-top: 8px;
}

/* Фото с бордовой рамкой */
.photo-wrapper {
  border: 6px solid #7a1a1a;
  width: 100%;
  aspect-ratio: 4 / 3;
  max-width: 320px;
  margin: 0 auto;

  overflow: hidden;
  background: #000; /* на случай, если фото не загрузилось */

  box-shadow:
    0 30px 60px rgba(0,0,0,0.3),
    0 1px 1px rgba(0,0,0,0.4) inset;

  flex-shrink: 0;
}

.photo-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

/* Нижняя строка с линией и датой */
.bottom-row {
  margin-top: auto;        /* КЛЮЧЕВОЕ: прижимает блок в самый низ карточки */
  width: 100%;
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: flex-end;
  row-gap: 12px;
}

/* линия слева-вниз (имитация угловой рамки с тенью) */
.bottom-line {
  width: 100%;
  max-width: 90%;
  border-bottom: 1px solid #7a1a1a;
  box-shadow: 0 25px 40px rgba(0,0,0,0.15);
  align-self: flex-start;
}

/* дата в правом нижнем углу */
.date {
  font-size: 0.8rem;
  letter-spacing: 0.3em;
  color: #2b2b2b;
  font-family: "Playfair Display", Georgia, serif;
  text-align: right;
  padding-right: 8px;
}
  
/* Мобильчик: пусть контент не вылезает, но остаётся плотным */
@media (max-width: 480px) {
  .invite-card {
    max-width: 100%;
    border-radius: 0;
    box-shadow: none; /* на настоящем айфоне тень почти не видна */
  }

  .invite-layout {
    padding-top: 70px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 16px;
  }

  .name-left,
  .name-right {
    font-size: 2rem;
  }

  .photo-wrapper {
    max-width: 300px;
    border-width: 5px;
  }

  .bottom-line {
    max-width: 92%;
  }

  .date {
    font-size: 0.75rem;
    letter-spacing: 0.25em;
    padding-right: 4px;
  }
}
