/* фон всей секции — как вокруг телефона на скрине */
.address-page {
  min-height: auto;
  width: 100%;
 background: radial-gradient(circle at 50% 0%, #ffffff 0%, #f3f3f3 70%);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 60px 16px 80px;
  box-sizing: border-box;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

/* внешний бордовый прямоугольник */
.card-outer {
  background-color: #7a1a1a;            /* глубокий винно-бордовый */
  width: 100%;
  max-width: 360px;                      /* мобильная визитка */
  min-height: 480px;
  box-shadow: 0 25px 50px rgba(0,0,0,0.4);
  position: relative;
  padding: 16px;
  box-sizing: border-box;
  display: flex;
  align-items: stretch;
  justify-content: center;
}

/* внутренняя тонкая рамка (почти как золотистая/светлая линия на скрине) */
.card-inner {
  border: 1px solid rgba(255,255,255,0.6);
  padding: 24px 16px 24px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  color: #fdfdfd;
  text-align: center;
  row-gap: 20px;
}

/* красивый рукописный заголовок */
.card-title {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.2;
  color: rgba(255,255,255,0.9);
  text-align: center;
  margin: 0;
  /* немного воздуха снизу, как на рефе */
  padding-bottom: 8px;
}

/* блок с фотографией */
.card-photo {
  width: 100%;
  background: rgba(0,0,0,0.2);
  overflow: hidden;
  max-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* на скрине фото приглушено, почти ч/б */
}

/* нижний текст с адресом */
.card-footer-text {
  font-family: "Playfair Display", Georgia, serif;
  font-size: 0.95rem;
  line-height: 1.4;
  font-weight: 400;
  color: rgba(255,255,255,0.8);
  text-align: center;
  padding-top: 12px;
  border-top: 1px solid rgba(255,255,255,0.4); /* как отдельный нижний блок */
}

/* адаптивчик */
@media (max-width: 400px) {
  .card-outer {
    max-width: 330px;
    padding: 14px;
  }

  .card-inner {
    padding: 20px 14px;
    row-gap: 18px;
  }

  .card-title {
    font-size: 1.8rem;
  }

  .card-footer-text {
    font-size: 0.9rem;
    line-height: 1.4;
  }
}
/* кнопка под текстом */
.map-button {
  display: inline-block;
  margin: 10px auto 0;
  background-color: #fdfdfd;
  color: #7a1a1a;
  font-family: "Playfair Display", Georgia, serif;
  font-size: 0.95rem;
  font-weight: 500;
  text-decoration: none;
  padding: 10px 20px;
  border-radius: 30px;
  border: 1px solid rgba(255,255,255,0.4);
  transition: all 0.3s ease;
}

.map-button:hover {
  background-color: #7a1a1a;
  color: #fff;
  border-color: #fff;
  transform: translateY(-2px);
}

@media (max-width: 400px) {
  .map-button {
    font-size: 0.9rem;
    padding: 9px 18px;
  }
}
