/* фон всей секции */
.details-wrapper {
  width: 100%;
  min-height: 70vh;

  /* мягкая праздничная бумага, слегка тёплая */
  background: radial-gradient(circle at 50% 0%, #f3f3f3 0%, #ffffff 70%);


  
  display: flex;
  justify-content: center;
  align-items: flex-start;

  padding: 48px 16px 80px;
  box-sizing: border-box;
  color: #2b2b2b;

  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

/* сама карточка с текстом */
.details-card {
  position: relative;
  width: 380px;
  max-width: 90vw;

  background: #fffdfb;
  border: 1px solid rgba(122, 26, 26, 0.4); /* бордовый контур с прозрачностью */
  box-shadow:
    0 50px 120px rgba(0, 0, 0, 0.07),
    0 2px 3px rgba(0, 0, 0, 0.4) inset;

  border-radius: 12px;
  padding: 32px 20px 72px; /* снизу больше, чтобы там красиво сидела печать */
  text-align: center;

  /* тонкая элегантная рамка внутри, имитирует печатную типографию */
  background-image:
    radial-gradient(circle at 50% 0%, rgba(255,255,255,0.7) 0%, rgba(247,243,239,0.4) 80%);
  box-sizing: border-box;
}

/* рукописный заголовок */
.details-heading {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 2.2rem;
  font-weight: 400;
  line-height: 1.2;
  color: #2b2b2b;
  margin: 0 0 24px 0;
  text-rendering: optimizeLegibility;
}

/* основной текст */
.details-text {
  font-family: "Playfair Display", Georgia, serif;
  font-size: 1rem;
  line-height: 1.6;
  letter-spacing: 0.02em;
  color: #2b2b2b;
  margin: 0 0 20px 0;
}

.details-text br {
  line-height: 1.6;
}

/* второстепенный блок текста — делаем мягче */
.details-text.note {
  color: rgba(43, 43, 43, 0.7);
  font-size: 0.95rem;
  line-height: 1.5;
  margin-top: 8px;
}

/* "восковая печать" */
.details-seal {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -36px; /* печать слегка "вылезает" за карточку вниз */
  display: flex;
  justify-content: center;
  pointer-events: none;
}

.details-seal-ring {
  background: radial-gradient(circle at 30% 30%, #7a1a1a 0%, #7a1a1a 60%, #72392b 100%);
  width: 64px;
  height: 64px;
  border-radius: 50%;

  box-shadow:
    0 14px 30px rgba(0,0,0,0.4),
    0 0 3px rgba(0,0,0,0.6) inset,
    0 -2px 2px rgba(255,255,255,0.4) inset;
  border: 2px solid rgba(0,0,0,0.3);

  display: flex;
  align-items: center;
  justify-content: center;

  position: relative;
}

/* декоративный бордюр вокруг печати, имитация штампа */
.details-seal-ring::after {
  content: "";
  position: absolute;
  inset: 6px;
  border-radius: 50%;
  border: 1px solid rgba(0,0,0,0.4);
  box-shadow:
    0 0 2px rgba(0,0,0,0.6) inset,
    0 2px 2px rgba(255,255,255,0.4);
}

/* инициалы внутри печати */
.details-seal-initials {
  font-family: "Playfair Display", Georgia, serif;
  font-size: 0.8rem;
  line-height: 1;
  letter-spacing: 0.05em;
  color: #fbe5c7;
  position: relative;
  z-index: 2;
  text-shadow:
    0 1px 1px rgba(0,0,0,0.7),
    0 0 2px rgba(0,0,0,0.8);
}

/* адаптив для телефонов */
@media (max-width: 480px) {
  .details-card {
    max-width: 360px;
    padding: 28px 16px 68px;
  }

  .details-heading {
    font-size: 2rem;
  }

  .details-text {
    font-size: 0.95rem;
    line-height: 1.5;
  }

  .details-text.note {
    font-size: 0.9rem;
  }

  .details-seal-ring {
    width: 58px;
    height: 58px;
  }

  .details-seal-initials {
    font-size: 0.7rem;
  }
}
