/* ВЕСЬ ЭКРАН */
.countdown-page {
  width: 100%;
  min-height: 100vh;
  background: radial-gradient(circle at 50% 0%, #ffffff 0%, #efefef 70%);
  display: flex;
  justify-content: center;
  align-items: flex-start;

  padding: 40px 16px 80px;
  box-sizing: border-box;

  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
  color: #000;
}

/* ВНУТРЕННИЙ КОНТЕЙНЕР (узкий как телефон на скрине) */
.countdown-wrapper {
  width: 380px;
  max-width: 90vw;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;

  color: #2b2b2b;
}

/* "До свадьбы" рукописным */
.countdown-script {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 2.4rem;
  font-weight: 400;
  line-height: 1.2;
  color: #2b2b2b;
  margin: 0 0 24px 0;
  text-rendering: optimizeLegibility;
}

/* "ОСТАЛОСЬ:" капсом */
.countdown-left-label {
  font-family: "Playfair Display", Georgia, serif;
  font-size: 0.9rem;
  line-height: 1.2;
  color: #2b2b2b;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  margin-bottom: 24px;
}

/* ЛИНЕЙНЫЙ ТАЙМЕР */
.timer-line {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: nowrap;
  gap: 12px;
  color: #2b2b2b;
  font-family: "Playfair Display", Georgia, serif;
  margin-bottom: 48px;
}

.timer-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 50px;
}

/* само число */
.timer-num {
  font-size: 1.9rem;
  line-height: 1.2;
  font-weight: 400;
  color: #2b2b2b;
  transition: transform 0.4s;
}

/* подпись под числом */
.timer-sub {
  margin-top: 6px;
  font-size: 0.7rem;
  line-height: 1.2;
  font-family: "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, sans-serif;
  font-weight: 400;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: rgba(0,0,0,0.6);
}

/* вертикальная палка-разделитель */
.timer-sep {
  font-size: 1.9rem;
  line-height: 1.2;
  font-weight: 300;
  font-family: "Playfair Display", Georgia, serif;
  color: rgba(0,0,0,0.6);
  margin-top: -2px;
}

/* анимация смены числа (оставляем твою идею flip) */
.flip {
  animation: flipOnce 0.6s ease;
}
@keyframes flipOnce {
  0%   { transform: rotateX(0deg); }
  50%  { transform: rotateX(180deg); }
  100% { transform: rotateX(360deg); }
}

/* "конверт" блок снизу */
.envelope-card {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

/* серая рамка как на скрине телефона */
.envelope-inner {
  width: 100%;
  border: 1px solid rgba(0,0,0,0.4);
  background: #efefef;
  background-image: radial-gradient(circle at 50% 0%, #ffffff 0%, #efefef 80%);
  box-sizing: border-box;

  padding: 24px 16px 80px;
  position: relative;
}

/* "Будем вас ждать!" рукописно */
.envelope-title {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.2;
  color: #2b2b2b;
  text-align: center;
  margin-bottom: 24px;
}

/* фотки внутри "конверта" */
.envelope-photos {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;

  /* имитируем, что фото как будто вложены внутрь конверта */
  position: relative;
  z-index: 2;
}

.envelope-photo-main {
  width: 220px;
  max-width: 80%;
  height: auto;
  border-radius: 4px;
  box-shadow: 0 8px 20px rgba(0,0,0,0.25);
  object-fit: cover;
  object-position: center;
}

/* Заглушка красного конверта (та самая бордовая бумага с печатью).
   Сейчас просто блок, ты потом заменишь на PNG поверх.
   Он прижат к низу и перекрывает низ фоток. */
.envelope-red-placeholder {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;

  height: 140px; /* примерно как на скрине */
  background: #7a1a1a;

  clip-path: polygon(
    0% 40%,
    50% 0%,
    100% 40%,
    100% 100%,
    0% 100%
  );

  /* имитация восковой печати можно потом сделать отдельным кружком PNG */
  z-index: 1;
}

/* адаптив: на телефоне у нас и так одна колонка,
   но уменьшим шрифты, чтобы влазило как в iPhone-экране */
@media (max-width: 480px) {
  .countdown-wrapper {
    max-width: 360px;
  }

  .countdown-script {
    font-size: 2.2rem;
  }

  .countdown-left-label {
    font-size: 0.8rem;
    margin-bottom: 20px;
  }

  .timer-line {
    gap: 10px;
    margin-bottom: 40px;
  }

  .timer-num {
    font-size: 1.7rem;
  }

  .timer-sep {
    font-size: 1.7rem;
  }

  .envelope-title {
    font-size: 1.6rem;
  }

  .envelope-photo-main {
    width: 200px;
  }

  .envelope-red-placeholder {
    height: 130px;
  }
}
